import React from "react";
const CatalogCard = ({
  entry,
  id,
  modalVisible,
  setModalVisible,
  setSelectedEntry,
}) => {
  const maxTags = 2;
  const tagsList = entry.tags.slice(0, maxTags).map((data, i) => {
    return (
      <div
        className="rounded-sm text-xs inline-block mr-1 border mb-1 py-1 px-2"
        key={"tagrow" + i}
      >
        {data}{" "}
      </div>
    );
  });
  const onCardClick = (e) => {
    setSelectedEntry(id);
    setModalVisible(!modalVisible);
  };
  return (
    <div
      onClick={onCardClick}
      className="hover:shadow-lg shadow bg-white cursor-pointer rounded-sm flex flex-col h-full "
    >
      <img
        alt={entry.short_description}
        className="rounded rounded-b-none h-32 object-cover"
        src={entry.image_path}
      />
      <div className="flex-grow p-3 font-semibold  ">{entry.title}</div>
      <div className="  p-3 "> {tagsList} </div>
    </div>
  );
};

export default CatalogCard;
