import React, { useEffect, useState } from "react";
import "./landing.css";

import { Spin } from "antd";
import CatalogGrid from "./CatalogGrid";
// import CatalogFeatures from "./CatalogFeatures";
// import Icons from "../helpers/Icons";

const yaml = require("js-yaml");

const PlaneLanding = () => {
  const [yamlLoaded, setYamlLoaded] = useState(false);
  const [catalogEntries, setCatalogEntries] = useState(null);

  const yamlConfigUrl =
    "https://raw.githubusercontent.com/cloudera/Applied-ML-Prototypes/master/amp-catalog-cloudera-default.yaml";

  useEffect(() => {
    document.title = `Cloudera Applied Machine Learning Prototypes | Catalog `;
    fetch(yamlConfigUrl)
      .then((data) => {
        data.text().then((yamlText) => {
          yaml.loadAll(yamlText, function (doc) {
            setYamlLoaded(true);
            setCatalogEntries(doc);
          });
        });
      })
      .catch((error) => {
        console.log(error);
        setYamlLoaded(true);
      });
  }, []);

  return (
    <div className="text-gray-600 h-screen">
      {!yamlLoaded && (
        <div className="mt-2">
          <Spin /> <span className="ml-1"> Loading YAML data ... </span>
        </div>
      )}

      {/* <div className="-mt-10"></div> */}
      <div className="px-4">
        <div className="container-fluid   mx-4">
          <div className="mt-4   mb-20">
            <CatalogGrid entries={catalogEntries?.entries} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default PlaneLanding;
