import React from "react";
import { Modal } from "antd";
import Icons from "../helpers/Icons";

const CatalogModal = ({ entry, modalVisible, setModalVisible }) => {
  const maxTags = 10;

  const tagsList = entry.tags.slice(0, maxTags).map((data, i) => {
    return (
      <div
        className="rounded-sm text-xs inline-block mr-1 border mb-1 py-1 px-2 text-gray-700"
        key={"tagrow" + i}
      >
        {data}{" "}
      </div>
    );
  });
  // console.log(entry);
  return (
    <Modal
      title={entry.title}
      visible={modalVisible}
      // onOk={() => createProject(catalogEntry)}
      okText={
        <a target="_blank" rel="noreferrer" href={entry.git_url}>
          <Icons icon="github" size={4} /> View on Github
        </a>
      }
      // okButtonProps={{ disabled: catalogEntry.comingSoon }}
      // confirmLoading={isSubmitting}
      onCancel={() => setModalVisible(false)}
    >
      <div className="mb-4">{entry.long_description}</div>
      <div>{tagsList}</div>
      <div className="mt-3 text-sm">
        {" "}
        You can run this prototype in your own environment or automatically
        deploy it on{" "}
        <a
          target="_blank"
          rel="noreferrer"
          href="https://www.cloudera.com/products/cloudera-data-platform/test-drive.html"
        >
          {" "}
          Cloudera Machine Learning
        </a>
        .
      </div>
    </Modal>
  );
};

export default CatalogModal;
